/**
 * Grid
 */
.flex-small,
.flex-large {
  padding-left: 1rem;
  padding-right: 1rem;
}

.flex-row {
  margin-left: -1rem;
  margin-right: -1rem;
}

.flex-row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.flex-small,
.flex-large {
  flex-basis: 100%;
  margin-bottom: 1rem;
}

/* Small screen breakpoint */
@media (min-width: 600px) {
  .flex-small {
    flex: 1 1;
    margin-bottom: 0;
  }
  .flex-small.half {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(1 / 2 * 100%);
  }
  .flex-small.one-fourth {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(1 / 4 * 100%);
  }
  .flex-small.three-fourths {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(3 / 4 * 100%);
  }
  .flex-small.one-third {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(1 / 3 * 100%);
  }
  .flex-small.two-thirds {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(2 / 3 * 100%);
  }
}

/* Large screen breakpoint */
@media (min-width: 1000px) {
  .flex-large {
    flex: 1 1;
    margin-bottom: 0;
  }
  .flex-large.half {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(1 / 2 * 100%);
  }
  .flex-large.one-fourth {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(1 / 4 * 100%);
  }
  .flex-large.three-fourths {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(3 / 4 * 100%);
  }
  .flex-large.one-third {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(1 / 3 * 100%);
  }
  .flex-large.two-thirds {
    flex-grow: 0;
    flex-shrink: 0;
    flex-basis: calc(2 / 3 * 100%);
  }
}

